import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
// import Carousel from 'react-elastic-carousel'
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import Teacher1 from './img/teacher1.png';
import Teacher2 from './img/teacher2.png';
import Teacher3 from './img/teacher3.png';
import Teacher4 from './img/teacher4.png';
import Teacher5 from './img/teacher5.png';
import Teacher6 from './img/teacher6.png';
import './teacher.css';


const photos = [
    {
        id: '01',
        Ename: 'WANG, CHENG-PING',
        name: '',
        education: '',
        expertise: '',
        src: Teacher1
    },{
        id: '02',
        Ename: 'LIN, CHANG-YU',
        name: '',
        education: 'Kaiserslautern',
        expertise: '',
        src: Teacher2
    },{
        id: '03',
        Ename: 'CHIEN, PO-HSIU',
        name: '',
        education: '',
        expertise: '',
        src: Teacher3
    },{
        id: '04',
        Ename: 'LIU, SUNG-TA',
        name: '',
        education: '',
        expertise: '',
        src: Teacher4
    },{
        id: '05',
        Ename: 'HUANG, SHU-CHUN',
        name: '',
        education: '',
        expertise: '',
        src: Teacher5
    },{
        id: '06',
        Ename: 'HUANG,YUEH-WEN',
        name: '',
        education: '',
        expertise: '',
        src: Teacher6
    }
]
class TeacherDetail extends Component{

    render() {
        const settings = {
            infinite: true,
            speed: 500,
            slidesToScroll: 1, 
            className: "slides",
            centerMode: true,
            centerPadding: '60px',
            slidesToShow: 3,
            responsive: [
                {
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                  }
                },
                {
                  breakpoint: 600,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                  }
                }
              ]
        }
        return (
            <div className="App">
                <Slider {...settings}>
                {photos.map((photo, key) => {
                    return (
                        <Row className="teacher-row">
                            <Col md="12" sm="12" className="teacher-img">
                                <span className="num">0{ key + 1 }<p className="e-name">{photo.Ename}</p></span>
                                <div className="img"><img src={photo.src} alt={photo.name} /></div>
                            </Col>
                            <Col md="11" sm="11" className="teacher-description">
                                <p className="name">{photo.name}</p>
                                <span className="subject">/{photo.education}</span><br />
                                <span className="subject">/{photo.expertise}</span>
                            </Col>
                        </Row>
                    )
                })}
                </Slider>
            </div>
        )
    }
}
export default TeacherDetail;

